import React, { useState, useEffect, forwardRef, useRef } from "react";
import CircularProgressWithLabel from "./CircularProgressWithLabel";
import { setMode } from "../redux/settingsSlice";
import { POMODORO, SHORT_BREAK, LONG_BREAK } from "../constants/appConfig";
import { sendNotification } from "../utils/notifications";
import { updateTitle, isMobileDevice } from "../utils/helperFunctions";
import { useDispatch } from "react-redux";

const Timer = (
  {
    pomodoroTime,
    longBreakTime,
    shortBreakTime,
    autoStartPomodoroEnabled,
    autoStartEnabled,
    handleResetClick,
    handleAutoStart,
    isRunning,
    mode,
    alarmAudio,
    tickingAudio,
  },
  ref
) => {
  const [minutes, setMinutes] = useState(pomodoroTime);
  const [seconds, setSeconds] = useState(0);
  const [progressBarValue, setProgressBarValue] = useState(0);
  // Bin  theo di thi gian cn li
  const currentDurationRef = useRef(0);
  // S dng mt bin tham chiu ring  lu interval
  const intervalRef = useRef(null);
  const dispatch = useDispatch();

  useEffect(() => {
    console.log("mode", mode, 'running', isRunning);
    const handleTimerEnd = () => {
      console.log("autoStartEnabled", autoStartEnabled);
      console.log("autoStartPomodoroEnabled", autoStartPomodoroEnabled);
      alarmAudio.play();
      tickingAudio.stop();
      handleResetClick();
      switch (mode) {
        case POMODORO:
          sendNotification("Pomodoro kt thc", "Hy ngh ngn 5 pht!");
          if (autoStartEnabled) {
            console.log("Pomodoro kt thc autoStartEnabled", autoStartEnabled);
            dispatch(setMode(SHORT_BREAK)); // Chuyn sang ch  Short Break
            console.log('GOi vao day ko');
            handleAutoStart(); // Bt u m ngc cho Short Break
          }
          break;
        case SHORT_BREAK:
          sendNotification("Ngh kt thc", "Bt u Pomodoro tip theo!");
          if (autoStartPomodoroEnabled) {
            console.log(
              "SHORT_BREAK kt thc autoStartEnabled",
              autoStartPomodoroEnabled
            );
            dispatch(setMode(POMODORO)); // Chuyn li ch  Pomodoro
            console.log('GOi vao day ko');
            handleAutoStart(); // Bt u m ngc cho Pomodoro
          }
          break;
        case LONG_BREAK:
          sendNotification("Ngh kt thc", "Bt u Pomodoro tip theo!");
          if (autoStartPomodoroEnabled) {
            console.log(
              "LONG_BREAK kt thc autoStartEnabled",
              autoStartEnabled
            );
            dispatch(setMode(POMODORO)); // Chuyn li ch  Pomodoro
            console.log('GOi vao day ko');
            handleAutoStart(); // Bt u m ngc cho Pomodoro
          }
          break;
        default:
          break;
      }
    };

    const startTimer = () => {
      console.log("startTimer", mode);
      const updateTimer = (duration) => {
        setMinutes(Math.floor(duration / 60));
        setSeconds(duration % 60);
      };
      const handleCountdown = () => {
        console.log("handleCountdown");
        let totalSecondsInMode;
        if (currentDurationRef.current > 0) {
          currentDurationRef.current -= 1;
          switch (mode) {
            case POMODORO:
              totalSecondsInMode = pomodoroTime * 60; // 25 pht cho Pomodoro
              break;
            case SHORT_BREAK:
              totalSecondsInMode = shortBreakTime * 60; // 5 pht cho Short Break
              break;
            case LONG_BREAK:
              totalSecondsInMode = longBreakTime * 60; // 15 pht cho Long Break
              break;
            default:
              break;
          }
          const progress =
            ((totalSecondsInMode - currentDurationRef.current) /
              totalSecondsInMode) *
            100;
          setProgressBarValue(progress);

          updateTimer(currentDurationRef.current);
        } else {
          clearInterval(ref.current);
          handleTimerEnd();
        }
      };

      intervalRef.current = setInterval(handleCountdown, 1000);
    };

    const pauseTimer = () => {
      clearInterval(intervalRef.current);
    };

    const resetTimer = () => {
      clearInterval(intervalRef.current);
      // Cp nht thi gian
      switch (mode) {
        case POMODORO:
          setMinutes(pomodoroTime);
          break;
        case SHORT_BREAK:
          setMinutes(shortBreakTime);
          break;
        case LONG_BREAK:
          setMinutes(longBreakTime);
          break;
        default:
          break;
      }
      setSeconds(0);

      // Cp nht gi tr progressBarValue
      setProgressBarValue(0);
    };
    ref.current = {
      start: startTimer,
      pause: pauseTimer,
      reset: resetTimer,
    };

    return () => {
      clearInterval(intervalRef.current);
    };
  }, [
    ref,
    isRunning,
    mode,
    pomodoroTime,
    shortBreakTime,
    longBreakTime,
    alarmAudio,
    autoStartEnabled,
    autoStartPomodoroEnabled,
    handleResetClick,
    tickingAudio,
  ]);

  // S dng useEffect  cp nht gi tr minutes v seconds khi gi tr thay i
  useEffect(() => {
    switch (mode) {
      case POMODORO:
        setMinutes(pomodoroTime);
        break;
      case SHORT_BREAK:
        setMinutes(shortBreakTime);
        break;
      case LONG_BREAK:
        setMinutes(longBreakTime);
        break;
      default:
        break;
    }
    setSeconds(0);
  }, [pomodoroTime, shortBreakTime, longBreakTime, mode]);

  useEffect(() => {
    const totalSeconds = minutes * 60 + seconds;
    currentDurationRef.current = totalSeconds;
    if (!isMobileDevice()) {
      updateTitle(minutes, seconds, mode);
    }
  }, [minutes, seconds, mode]);

  return (
    <CircularProgressWithLabel
      minutes={minutes}
      seconds={seconds}
      variant="determinate"
      value={progressBarValue}
    />
  );
};

// S dng React.forwardRef  chuyn tip ref t Props
export default forwardRef(Timer);
