// PomodoroInfo.js
import React from "react";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import { Box, Link } from "@mui/material";

function PomodoroInfo() {
  return (
    <Container maxWidth="sm">
      <Box
        sx={{
          p: 2,
        }}
      >
        <Typography variant="h6" component="h2" gutterBottom>
          Phng php Pomodoro l g?
        </Typography>
        <Typography variant="body1" component="blockquote">
          Phng php Pomodoro l mt phng php qun l thi gian c pht
          trin bi Francesco Cirillo vo cui nhng nm 1980. N s dng ng
          h bm gi trong bp  chia cng vic thnh cc khong thi gian,
          thng l 25 pht, gia mi khong mt thi gian ngh ngi ngn. Mi
          khong thi gian c gi l mt pomodoro, bt ngun t ting  c
          ngha l c chua, theo tn ng h bm gi dng trong bp hnh qu c
          chua m Cirillo  s dng khi cn l sinh vin i hc. {" "}
          <Link href="https://vi.wikipedia.org/wiki/Ph%C6%B0%C6%A1ng_ph%C3%A1p_Pomodoro">
            Phng php Pomodoro, Wikipedia
          </Link>
        </Typography>
        <Typography variant="h6" component="h2" gutterBottom>
          Mt s l do ti sao nn s dng k thut Pomodoro
        </Typography>
        <Box
          sx={{
            px: 2,
          }}
          component="ul"
        >
          <Box component="li">
            <strong>Tng s tp trung</strong>: K thut Pomodoro yu cu bn
            lm vic hoc hc tp trong khong thi gian ngn v gii hn
            (thng l 25 pht). iu ny gip bn tp trung ti a vo cng
            vic trong khong thi gian .
          </Box>
          <Box component="li">
            <strong>Gim cng thng v mt mi</strong>: Vic chia cng vic
            thnh cc khong thi gian ngn kt hp vi cc khong thi gian
            ngh ngn (nh 5 pht) hoc di hn (nh 15 pht) gip bn trnh
            cng thng v mt mi.
          </Box>
          <Box component="li">
            <strong>Tng nng sut</strong>: Pomodoro gip bn lm vic hiu qu
            hn bng cch to p lc thi gian. Vic hon thnh cng vic trong
            khong thi gian ngn c th thc y bn hon thnh nhiu cng vic
            hn trong mt ngy.
          </Box>
          <Box component="li">
            <strong>D dng theo di thi gian</strong>: Pomodoro c thi gian
            c nh (25 pht) nn bn c th d dng theo di thi gian v nh
            gi tin  cng vic ca mnh.
          </Box>
          <Box component="li">
            <strong>H tr qun l thi gian</strong>: Bng cch s dng
            Pomodoro, bn c th qun l thi gian ca mnh mt cch hiu qu
            hn. Bn bit c mnh  lm vic trong bao nhiu thi gian v c
            th iu chnh thi gian ngh  m bo s cn bng gia lm vic
            v th gin.
          </Box>
          <Box component="li">
            <strong>Khc phc tnh trng tr hon</strong>: K thut Pomodoro
            khuyn khch bn bt u cng vic ngay lp tc v lm vic lin tc
            trong khong thi gian Pomodoro. iu ny c th gip bn vt qua
            tnh trng tr hon.
          </Box>
          <Box component="li">
            <strong>D p dng</strong>: Pomodoro khng i hi cng c phc tp
            hoc k thut c bit. Bn ch cn mt b hn gi hoc mt ng dng
            m gi n gin  bt u.
          </Box>
          <Box component="li">
            <strong>Ci thin qun l d n</strong>: Pomodoro c th p dng
            cho qun l d n  chia d n thnh cc nhim v nh hn v theo
            di tin trnh mt cch c th.
          </Box>
        </Box>

        <Typography variant="h6" component="h2" gutterBottom>
          5 bc s dng phng php Pomodoro  tng hiu sut v qun l thi
          gian hiu qu
        </Typography>
        <Box
          sx={{
            px: 2,
          }}
          component="ul"
        >
          <Box component="li">
            <strong>Chn cng vic cn thc hin</strong>: Bc u tin l xc
            nh cng vic hoc nhim v bn mun hon thnh. m bo cng vic
            ny c th v c th thc hin trong mt khong thi gian tng i
            ngn.
          </Box>
          <Box component="li">
            <strong>Thit lp hn gi Pomodoro</strong>: Bt u mt m gi
            Pomodoro trong khong thi gian ti u (thng l 25 pht). Khi bt
            u, tp trung 100% vo cng vic m bn  chn v c gng hon
            thnh nhim v  trong khong thi gian Pomodoro.
          </Box>
          <Box component="li">
            <strong>Lm vic cho n khi ht thi gian</strong>: Trong khong
            thi gian Pomodoro, lm vic khng gin on v tp trung hon ton
            vo cng vic. Trnh xem in thoi, mng x hi hoc bt k s xao
            lnh no khc.
          </Box>
          <Box component="li">
            <strong>Khi Pomodoro kt thc, ngh ngn</strong>: Sau khi Pomodoro
            kt thc (v d, sau 25 pht lm vic), hy chp nhn mt khong
            thi gian ngn ngh ngi (thng l 5 pht). S dng thi gian ny
             th gin, ng dy, cn chnh v chun b cho Pomodoro tip theo.
          </Box>
          <Box component="li">
            <strong>Lp li hoc ngh ngn di</strong>: Sau khi hon thnh mt
            chu k Pomodoro v ngh ngn, bn c th lp li bc 2 v 3 cho
            cng vic tip theo. Sau bn chu k Pomodoro lin tip, bn c th
            thc hin mt khong ngh di hn (thng l 15-30 pht)  th gin
            hon ton.
          </Box>
        </Box>

        <Typography variant="h6" component="h2" gutterBottom>
          Sau khi mt chu k Pomodoro kt thc bn c th lm g?
        </Typography>
        <Box
          sx={{
            px: 2,
          }}
          component="ul"
        >
          <Box component="li">
            <strong>Ngh ngn</strong>: Thc hin mt khong ngh ngn ngay sau
            khi Pomodoro kt thc. Thi gian ngh ngn thng l 5 pht. Trong
            khong thi gian ny, bn c th ng dy, vn ng c th, cn
            chnh li t duy, v ly mt hi  sn sng cho Pomodoro tip theo.
          </Box>
          <Box component="li">
            <strong>Ghi chp cng vic</strong>: S dng thi gian ngh ngn 
            ghi chp cng vic  hon thnh trong Pomodoro trc . iu ny
            gip bn theo di tin trnh cng vic v ci thin kh nng qun l
            thi gian.
          </Box>
          <Box component="li">
            <strong>Ngh di (Long Break)</strong>: Sau khi hon thnh mt s
            chu k Pomodoro (thng sau bn), bn c th thc hin mt khong
            ngh di hn, thng l t 15 n 30 pht. Trong khong thi gian
            ny, bn c th th gin hon ton, n mt ba nh, ra ngoi do
            chi, hoc lm bt k iu g gip bn ly li nng lng.
          </Box>
          <Box component="li">
            <strong>nh gi v iu chnh</strong>: S dng thi gian ngh 
            xem xt cng vic bn  hon thnh v xem xt liu bn c cn iu
            chnh mc tiu hoc u tin cng vic khng. iu ny gip bn m
            bo bn ang lm vic vo hng ng.
          </Box>
          <Box component="li">
            <strong>Nm vn ng</strong>: Trong khong ngh, bn c th thc
            hin mt s ng tc nh  gim cng thng c bp v tng tun hon
            mu.
          </Box>
          <Box component="li">
            <strong>Nm vn ng</strong>: Trong khong ngh, bn c th thc
            hin mt s ng tc nh  gim cng thng c bp v tng tun hon
            mu.
          </Box>
          <Box component="li">
            <strong>Tn dng thi gian ngh</strong>: S dng thi gian ngh 
            lm nhng cng vic nh nhng hoc th gin. V d, bn c th lp
            lch gi in thoi, kim tra email, hoc c mt cht.
          </Box>
        </Box>
      </Box>
    </Container>
  );
}

export default PomodoroInfo;
