import React, { useState, useEffect, useRef, useCallback } from "react";
import { useDispatch } from "react-redux";
import Timer from "./Timer";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { styled } from "@mui/material/styles";
import { setMode } from "../redux/settingsSlice";
import { POMODORO, SHORT_BREAK, LONG_BREAK } from "../constants/appConfig";
import { player } from "../utils/player";

const StyledTabs = styled((props) => (
  <Tabs
    {...props}
    TabIndicatorProps={{ children: <span className="MuiTabs-indicatorSpan" /> }}
  />
))({
  "& .MuiTabs-indicator": {
    display: "flex",
    justifyContent: "center",
    backgroundColor: "transparent",
  },
  "& .MuiTabs-indicatorSpan": {
    width: "100%",
    backgroundColor: "rgb(255, 255, 255)",
  },
});

const StyledTab = styled((props) => <Tab disableRipple {...props} />)(
  ({ theme }) => ({
    textTransform: "none",
    color: "rgba(255, 255, 255, 0.7)",
    "&.Mui-selected": {
      color: "#fff",
    },
    "&.Mui-focusVisible": {
      backgroundColor: "rgba(100, 95, 228, 0.32)",
    },
  })
);

const clickSound = player({
  asset: "audio/button-press.wav",
  volume: 0.5,
});

function PomodoroContent({
  pomodoroTime,
  shortBreakTime,
  longBreakTime,
  alarmAudio,
  tickingAudio,
  tickingSound,
  autoStartEnabled,
  autoStartPomodoroEnabled,
  mode,
}) {
  const [isRunning, setIsRunning] = useState(false);
  const dispatch = useDispatch();

  // S dng useRef  lu tr timerRef
  const timerRef = useRef(null);

  const toggleTimer = useCallback(() => {
    const playClickSound = () => {
      clickSound.play();
    };

    setIsRunning((prevIsRunning) => {
      if (prevIsRunning) {
        tickingAudio.stop(); // Dng m thanh khi tt
      }
      playClickSound(); // Pht m thanh trc khi thay i isRunning
      return !prevIsRunning; // S dng prevState  m bo tnh ton vn
    });
  }, [tickingAudio]);

  // Cp nht hm toggleTimer vo useEffect
  useEffect(() => {
    const handleSpacebarPress = (event) => {
      if (event.key === " ") {
        event.preventDefault(); // Ngn trnh duyt scroll mc nh
        toggleTimer();
      }
    };

    document.addEventListener("keydown", handleSpacebarPress);

    return () => {
      document.removeEventListener("keydown", handleSpacebarPress);
    };
  }, [toggleTimer]);

  // Hm bt s kin khi bm nt Reset
  const handleResetClick = () => {
    setIsRunning(false);
    tickingAudio.stop();
    timerRef.current.reset();
  };

  // Hm bt s kin khi bm nt Reset
  const handleAutoStart = () => {
    console.log('auto start nhe');
    clickSound.play();
    setIsRunning(true);
  };

  const switchMode = (event, newMode) => {
    // Ngng timer nu ang chy
    setIsRunning(false);
    // Cp nht ch  v thi gian
    dispatch(setMode(newMode));
  };

  // S dng useEffect  theo di gi tr isRunning v thc hin cc tng tc vi Timer.js
  useEffect(() => {
    if (isRunning) {
      console.log('Vao dayyyyyyyyyyyy');
      // Bt u m ngc khi isRunning l true
      timerRef.current.start();
    } else {
      // Dng m ngc khi isRunning l false
      timerRef.current.pause();
    }
  }, [isRunning]);

  return (
    <Box
      sx={{
        py: 2,
        minHeight: "calc(100vh - 64px)",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        backgroundColor: `${mode}.main`,
      }}
    >
      <StyledTabs
        textColor="secondary"
        indicatorColor="secondary"
        value={mode}
        onChange={switchMode}
        centered
      >
        <StyledTab value={POMODORO} label="Pomodoro" />
        <StyledTab value={SHORT_BREAK} label="Ngh ngn" />
        <StyledTab value={LONG_BREAK} label="Ngh di" />
      </StyledTabs>
      <Timer
        pomodoroTime={pomodoroTime}
        longBreakTime={longBreakTime}
        shortBreakTime={shortBreakTime}
        autoStartEnabled={autoStartEnabled}
        autoStartPomodoroEnabled={autoStartPomodoroEnabled}
        ref={timerRef}
        isRunning={isRunning}
        mode={mode}
        alarmAudio={alarmAudio}
        tickingAudio={tickingAudio}
        tickingSound={tickingSound}
        handleResetClick={handleResetClick}
        handleAutoStart={handleAutoStart}
      />
      <div className="controls">
        <Stack direction="row" spacing={2} justifyContent="center">
          <Button
            variant="contained"
            size="large"
            onClick={toggleTimer}
            color="secondary"
          >
            {isRunning ? "Tm dng" : "Bt u"}
          </Button>
          <Button onClick={handleResetClick} size="large" color="secondary">
            t li
          </Button>
        </Stack>
      </div>
    </Box>
  );
}

export default PomodoroContent;
