import { createSlice } from "@reduxjs/toolkit";
import {
  POMODORO,
  DEFAULT_POMODORO_TIME,
  DEFAULT_SHORT_BREAK_TIME,
  DEFAULT_LONG_BREAK_TIME,
} from "../constants/appConfig";
const savedSettings = localStorage.getItem("settings");

const initialState = savedSettings
  ? JSON.parse(savedSettings)
  : {
      pomodoroTime: DEFAULT_POMODORO_TIME, // Gi tr mc nh cho thi gian Pomodoro (minutes)
      volume: 50, // Gi tr mc nh cho m lng (t 0 n 1)
      shortBreakTime: DEFAULT_SHORT_BREAK_TIME, // Gi tr mc nh cho thi gian ngh ngn (minutes)
      longBreakTime: DEFAULT_LONG_BREAK_TIME, // Gi tr mc nh cho thi gian ngh di (minutes)
      alarmSound: "ALARM_DIGITAL", // Gi tr mc nh cho loi m thanh bo ng
      tickingSound: "TICKING_NONE", // Gi tr mc nh cho loi m thanh ng h
      hourFormat: "12", // Gi tr mc nh cho nh dng gi (12 hoc 24)
      autoStartPomodoroEnabled: false, // T ng bt u Pomodoro khi ng dng m
      autoStartEnabled: false, // T ng bt u khi kt thc Pomodoro hoc ngh
      mode: POMODORO,
    };

const settingsSlice = createSlice({
  name: "settings",
  initialState,
  reducers: {
    setPomodoroTime: (state, action) => {
      state.pomodoroTime = action.payload;
    },
    setVolume: (state, action) => {
      state.volume = action.payload;
    },
    setShortBreakTime: (state, action) => {
      state.shortBreakTime = action.payload;
    },
    setLongBreakTime: (state, action) => {
      state.longBreakTime = action.payload;
    },
    setAlarmSound: (state, action) => {
      state.alarmSound = action.payload;
    },
    setTickingSound: (state, action) => {
      state.tickingSound = action.payload;
    },
    setHourFormat: (state, action) => {
      state.hourFormat = action.payload;
    },
    setAutoStartPomodoroEnabled: (state, action) => {
      state.autoStartPomodoroEnabled = action.payload;
    },
    setAutoStartEnabled: (state, action) => {
      state.autoStartEnabled = action.payload;
    },
    setMode: (state, action) => {
      console.log('setMode', action.payload);
      state.mode = action.payload;
    },
  },
});

export const {
  setPomodoroTime,
  setVolume,
  setShortBreakTime,
  setLongBreakTime,
  setAlarmSound,
  setTickingSound,
  setHourFormat,
  setAutoStartPomodoroEnabled,
  setAutoStartEnabled,
  setMode,
} = settingsSlice.actions;

export default settingsSlice.reducer;
