export function sendNotification(title, body) {
  if (
    "Notification" in window &&
    "serviceWorker" in navigator &&
    "PushManager" in window
  ) {
    // Kim tra xem quyn thng bo  c cp cho ng dng hay cha
    if (Notification.permission === "granted") {
      //  cp quyn, bn c th hin th thng bo
      new Notification(title, {
        body: body,
        icon: "images/logo512.png",
        dir: "ltr",
      });
    } else if (Notification.permission === "denied") {
      // Quyn b t chi, hin th thng bo yu cu ngi dng cp quyn
      console.log("Vui lng cho php thng bo  s dng tnh nng ny.");
    } else {
      // Quyn cha c cp, hin th thng bo yu cu cp quyn
      Notification.requestPermission().then(function (permission) {
        if (permission === "granted") {
          // Quyn  c cp, bn c th hin th thng bo
          new Notification(title, {
            body: body,
            icon: "images/logo512.png",
            dir: "ltr",
          });
        } else {
          // Quyn b t chi, x l ty  (v d: hin th thng bo khc)
          console.log("Vui lng cho php thng bo  s dng tnh nng ny.");
        }
      });
    }
  } else {
    // Trnh duyt khng h tr API Notification
    console.log("Trnh duyt ca bn khng h tr thng bo.");
  }
}
